<template>
  <div style="background-color: white" v-if="competitors&&teams">
    <div v-if="isMobile">
      <div v-for="roster in teams[0]">
        <roster :stats="roster" :info="competitors[0].players[roster.esports_player_id]" :reverse="false"
                :border="false" :team-logo="competitors[0].logo.main.svg" :border-color="competitors[0].primaryColor"
                border-direction="left" :fold="false"></roster>
      </div>
      <div v-for="roster in teams[1]">
        <roster :stats="roster" :info="competitors[1].players[roster.esports_player_id]" :reverse="false"
                :border="false" :team-logo="competitors[1].logo.main.svg" :border-color="competitors[1].primaryColor"
                border-direction="left" :fold="false"></roster>
      </div>
    </div>
    <div v-else>
      <el-row v-if="teams!==undefined&&competitors!==undefined" type="flex">
        <el-col :span="12" class="left-rosters">
          <div v-for="roster in teams[0]">
            <roster :stats="roster" :info="competitors[0].players[roster.esports_player_id]" :reverse="true"
                    :border="false" :border-color="competitors[0].primaryColor" border-direction="left"
                    direction="left" :fold="false"></roster>
          </div>
        </el-col>


        <el-col :span="12" class="right-rosters">
          <div v-for="roster in teams[1]">
            <roster :stats="roster" :info="competitors[1].players[roster.esports_player_id]" :reverse="false"
                    :border="false" :border-color="competitors[1].primaryColor" border-direction="right"
                    direction="right" :fold="false"></roster>
          </div>
        </el-col>

      </el-row>

    </div>
  </div>
</template>

<script>
  import Roster from "@/components/Match/Roster";

  export default {
    name: "MapDetail",
    components: {Roster},
    props: ['teams', 'competitors'],
    mounted() {
    },
    computed: {
      isMobile() {
        return this.$store.getters.getWindowParam.width < 700;
      },
    }

  }
</script>

<style scoped>
  .map-detail {
    display: flex;
  }

</style>



