<template>
  <el-row class="map-brief-container" type="flex" v-if="data">
    <el-col :span="3" class="team-logo" :style="{backgroundColor:competitors[0].primaryColor}">
      <img :src="competitors[0].img" style="width: 100%">
    </el-col>
    <el-col :span="5" class="team-name-score" :class="[data.points!==undefined?'show-score':'']">
      <span>{{competitors[0].abbreviatedName}}</span>
      <span v-if="data.points!==undefined" :class="[concluded?leftWin?'win':'lose':'']">{{data.points[0]}}</span>
    </el-col>
    <el-col :span="8" class="map-container">
      <div :style="{backgroundImage:`url(${map.icon})`}"
           style=" background-size: cover;height: 100%;display: flex;flex-direction: column;justify-content: flex-end">
        <div style="background-color: #333333;color: white" class="map-name">{{map.name}}</div>
      </div>
    </el-col>
    <el-col :span="5" class="team-name-score flex-right" :class="[data.points!==undefined?'show-score':'']">
      <span v-if="data.points!==undefined" :class="[concluded?leftWin?'lose':'win':'']"> {{data.points[1]}}</span>
      <span>{{competitors[1].abbreviatedName}}</span>
    </el-col>
    <el-col :span="3" class="team-logo" :style="{backgroundColor:competitors[1].primaryColor}">
      <img :src="competitors[1].img" style="width: 100%">
    </el-col>

  </el-row>
</template>

<script>
  export default {
    name: "MapBrief",
    props: ['map', 'data', 'competitors'],
    computed: {
      concluded() {
        return this.data.state === 'CONCLUDED'
      },
      leftWin() {
        return this.data.points[0] > this.data.points[1]
      }
    }
  }
</script>

<style scoped>
  .map-brief-container {
    margin: 1rem -0.1rem;
    border: 0.1rem solid #d6d6d6;
  }

  .map-brief-team-logo {
    width: 100%;
  }

  .team-name-score {
    font-size: 2rem;
    padding: 1rem;
  }

  .team-logo, .team-name-score {
    display: flex;
    align-items: center;
  }

  .flex-right {
    justify-content: flex-end;
  }

  .show-score {
    justify-content: space-between;

  }

  .win {
    color: green;
  }

  .lose {
    color: red;
  }

  @media (max-width: 768px) {
    .team-name-score {
      font-size: .8rem;
      padding: .5rem;
    }
    .map-name{
      font-size: .7rem;
    }
  }

</style>



