<template>
  <div class="countdown">
    <div class="section">
      <div>{{day}}</div>
      <div>天</div>
    </div>
    <div class="section">
      <div>{{hour}}</div>
      <div>时</div>
    </div>
    <div class="section">
      <div>{{min}}</div>
      <div>分</div>
    </div>
    <div class="section">
      <div>{{sec}}</div>
      <div>秒</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Countdown",
    data() {
      return {
        day: undefined,
        hour: undefined,
        min: undefined,
        sec: undefined,
        flag: false
      }
    },
    props: ['endTime'],
    mounted() {
      let time = setInterval(() => {
        if (this.flag === true) {
          clearInterval(time)
        }
        this.timeDown()
      }, 500)
    },
    methods: {
      timeDown() {
        const endTime = new Date(this.endTime);
        const nowTime = new Date();
        let leftTime = parseInt((endTime.getTime() - nowTime.getTime()) / 1000);
        this.day = this.formatTime(parseInt(leftTime / (24 * 60 * 60)));
        this.hour = this.formatTime(parseInt(leftTime / (60 * 60) % 24));
        this.min = this.formatTime(parseInt(leftTime / 60 % 60));
        this.sec = this.formatTime(parseInt(leftTime % 60));
        if (leftTime <= 0) {
          this.flag = true;
          this.$emit('time-end')
        }
      },
      formatTime(time) {
        if (time >= 10) {
          return time
        } else {
          return `0${time}`
        }
      }


    }
  }
</script>

<style scoped>
  .section {
    width: 2rem;
    background-color: white;
  }

  .countdown {
    display: flex;
    font-size: 1rem;
  }

  @media (max-width: 768px) {
    .countdown {
      font-size: 0.7rem;

    }

    .section {
      width: 1rem;
    }

  }
</style>



