// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import './theme.scss'
import ElementUI from 'element-ui';
import App from './App'
import router from './router'
import OWL from '@/common/owl';
import Vuex from 'vuex'

const owl = new OWL({inChina: true});
import {teamLogos} from "@/common/util";

Vue.prototype.$owl = owl;
Vue.prototype.$teamLogos = teamLogos;
Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.use(Vuex);
let store = new Vuex.Store({
    state: {
      windowParam: {width: window.screen.availWidth, height: window.screen.availHeight},
      tabIndex: "0",
      search: '',
      ranks: {},
      season: 2019,
      players: {},
      maps: undefined,
      teams: {},
    },
    getters: {
      getRanks: (state) => (season) => {
        return state.ranks[season]
      },
      getSeason(state) {
        return state.season
      },
      getAllPlayersDetail: (state) => (season) => {
        return state.players[season]
      },
      getAllMaps(state) {
        return state.maps
      },
      getAllTeams(state) {
        return state.teams
      },
      getCurrentSearch(state) {
        return state.search
      },
      getWindowParam(state) {
        return state.windowParam
      },
      getCurrentTabIndex(state) {
        return state.tabIndex
      }
    },
    mutations: {
      setTeams(state, teams) {
        state.teams = teams;
      },
      setMaps(state, maps) {
        state.maps = maps
      },
      setPlayersDetail(state, {season, players, ranks}) {
        Vue.set(state.players, season, players);
        Vue.set(state.ranks, season, ranks)
      },
      seasonChange(state, season) {
        state.season = season;
      },
      searchChange(state, search) {
        state.search = search;
      },
      resize(state, windowParam) {
        state.windowParam = windowParam;
      },
      updateIndex(state, value) {
        state.tabIndex = value;
      }
    },
    actions: {
      async setTeams(context) {
        const a = await owl.allTeams();
        context.commit('setTeams', await owl.allTeams())
      },
      async setMaps(context) {
        context.commit('setMaps', await owl.allMaps())
      },
      async setPlayers(context, season) {
        const {players, ranks} = await owl.allPlayersDetailed({season: season});
        context.commit('setPlayersDetail', {season, players, ranks})
      },
      updateSeason(context, season) {
        context.commit('seasonChange', season)
      },
      updateSearch(context, search) {
        context.commit('searchChange', search)
      },
      updateWindowParam(context, windowParam) {
        context.commit('resize', windowParam)
      },
      updateCurrentTabIndex(context, index) {
        context.commit('updateIndex', index)
      }
    }
  })
;

router.beforeEach((to, from, next) => {
  switch (to.name) {
    case "Player":
      document.title = `${to.params.name} - ` + to.meta.title;
      store.dispatch('updateCurrentTabIndex', '1');
      break;
    case "Players":
      document.title = to.meta.title;
      store.dispatch('updateCurrentTabIndex', '1');
      break;
    case "Teams":
      document.title = to.meta.title;
      store.dispatch('updateCurrentTabIndex', '2');
      break;
    case "Matches":
      document.title = to.meta.title;
      store.dispatch('updateCurrentTabIndex', '3');
      break;
    case "Team":
      document.title = `${to.params.name} - ` + to.meta.title;
      store.dispatch('updateCurrentTabIndex', '2');
      break;
    case "Match":
      document.title = to.meta.title;
      store.dispatch('updateCurrentTabIndex', '3');
      break;
  }
  next()
});

new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>',
  created() {
    if (sessionStorage.redirect) {
      const redirect = sessionStorage.redirect;
      delete sessionStorage.redirect;
      this.$router.replace(redirect)
    }
  },

});



