import Vue from 'vue'
import Router from 'vue-router'
import Players from '@/components/Players'
import Player from '@/components/Player'
import Teams from "@/components/Teams/Teams";
import Matches from "@/components/Matches/Matches";
import Team from "@/components/Team/Team";
import Match from "@/components/Match/Match";
import Logo from "@/components/Logo";

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '/owl/',
  routes: [
    {
      path: '/players',
      name: 'Players',
      component: Players,
      meta: {
        title: "OWL Players"
      }
    },
    {
      path: '/player/:id/:name',
      name: 'Player',
      component: Player,
      meta: {
        title: "OWL Player"
      }
    },
    {
      path: '/teams/',
      name: 'Teams',
      component: Teams,
      meta: {
        title: "OWL Teams"
      }
    },
    {
      path: '/',
      name: 'Matches',
      component: Matches,
      meta: {
        title: "OWL Matches"
      }
    },
    {
      path: '/team/:id/:name',
      name: 'Team',
      component: Team,
      meta: {
        title: "OWL Team"
      }
    },
    {
      path: '/match/:id',
      name: 'Match',
      component: Match,
      meta: {
        title: "OWL Match"
      }
    },
     ]
})



