<template>
  <div class="match" v-loading="match===undefined">
    <div v-if="match!==undefined&&competitors!==undefined&&maps!==undefined">
      <el-row class="match-header" type="flex"
              :style="{background:`linear-gradient(to right, ${match.competitors[0].primaryColor} 50%, ${match.competitors[1].primaryColor} 50%)`}">
        <el-col :span="10" class="match-top-team"
                :style="{color:`${isBrightBg(match.competitors[0].primaryColor)?'black':'white'}`}">
          <div class="match-team-logo-container">
            <router-link :to="'/team/'+match.competitors[0].id+'/'+match.competitors[0].name">
              <img :src="competitors[0].img" class="match-team-logo">
            </router-link>
          </div>
          <span class="match-team-name">{{match.competitors[0].name}}</span>
        </el-col>
        <el-col :span="4" style="display: flex;align-items: center">
          <span class="match-status">
            <span v-if="match.state!=='PENDING'">
              <span :class="[concluded?leftWin?'win':'lose':'']">{{match.scores[0].value}}</span>
              <span class="divider"></span>
              <span :class="[concluded?leftWin?'lose':'win':'']">{{match.scores[1].value}}</span>
            </span>
            <span v-else><countdown :end-time="match.startDate"></countdown></span>
          </span>
        </el-col>
        <el-col :span="10" class="match-top-team flex-right"
                :style="{color:`${isBrightBg(match.competitors[1].primaryColor)?'black':'white'}`}">
          <span class="match-team-name">{{match.competitors[1].name}}</span>
          <div class="match-team-logo-container">
            <router-link :to="'/team/'+match.competitors[1].id+'/'+match.competitors[1].name">
              <img :src="competitors[1].img" class="match-team-logo">
            </router-link>
          </div>
        </el-col>

      </el-row>
      <div class="maps-container" v-if="Object.keys(maps).length !== 0">
        <el-col :span=24>
          <el-tabs type="border-card" value="all" class="tab-outside">
            <el-tab-pane label="总览" name="all">
              <div style="margin-top:1rem;font-size: 1.1rem">{{match.rule}}</div>
              <map-brief :map="maps[game.mapId]" :data="game" :competitors="competitors" v-for="game in match.games"
                         :key="game.id"></map-brief>
            </el-tab-pane>
            <el-tab-pane :label="maps[game.mapId].name" :name="game.mapId" v-for="game in match.games" :key="game.id">
              {{maps[game.mapId].type}}
              <div v-if="game.state==='PENDING'">尚未开始</div>
              <el-row v-else>
                <el-col :span="4" :offset="10" style="font-size: 1.5rem">
                  <span :span="10" :class="[game.points[0]>game.points[1]?'win':game.points[0]<game.points[1]?'lose':'']">
                    {{game.points[0]}}
                  </span>
                  <span :span="2">-</span>
                  <span :span="10" :class="[game.points[0]>game.points[1]?'lose':game.points[0]<game.points[1]?'win':'']">
                    {{game.points[1]}}
                  </span>
                </el-col>


              </el-row>
              <map-detail :competitors="competitors" :teams="games[game.mapId]"
                          v-if="fetchMapFinished"></map-detail>
            </el-tab-pane>
          </el-tabs>

        </el-col>

      </div>
    </div>
  </div>
</template>

<script>
  import {isBright} from "@/common/util";
  import Countdown from "@/components/Match/Countdown";
  import MapBrief from "@/components/Match/MapBrief";
  import MapDetail from "@/components/Match/MapDetail";

  export default {
    name: "Match",
    components: {MapDetail, MapBrief, Countdown},
    watch: {
      '$route'(to, from) {
        if (to.name === "Match")

          this.fetchMatch();
      },
    },
    data() {
      return {
        match: undefined,
        games: {},
      }
    },
    mounted() {
      this.fetchMatch()
    },
    computed: {
      maps() {
        return this.$store.getters.getAllMaps;
      },
      fetchMapFinished() {
        return Object.keys(this.games).length > 0
      },
      competitors() {
        return this.match !== undefined && Object.keys(this.$store.getters.getAllTeams).length > 0 ? [this.$store.getters.getAllTeams[this.match.competitors[0].id],
          this.$store.getters.getAllTeams[this.match.competitors[1].id]] : undefined

      },

      concluded() {
        return this.match.state === 'CONCLUDED'
      },
      leftWin() {
        return this.match.scores[0].value > this.match.scores[1].value
      }
    },
    methods: {
      isBrightBg(color) {
        return isBright(color.slice(1))
      },
      fetchMatch() {
        this.match = undefined;
        if (this.$store.getters.getAllMaps === undefined)
          this.$store.dispatch('setMaps');
        this.$owl.matchDetail(this.$route.params.id).then(o => {
          this.match = o;
          for (let i = 0; i < Object.keys(o.games).length; i++) {
            if (o.games[i].state === 'PENDING')
              break;
            this.$owl.gameDetail(o.id, i + 1).then(x => {
              this.$set(this.games, o.games[i].mapId, [x.teams.find(x => x['esports_team_id'] === o.competitors[0].id).players, x.teams.find(x => x['esports_team_id'] === o.competitors[1].id).players])
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  .match {
    font-weight: 700;
    font-size: 1rem;
    min-height: 50vh
  }

  .match-team-logo-container {
    height: 200px;
    width: 200px;
    padding: 1rem;
  }

  .match-header {
    font-size: 1.5rem;

  }

  .match-team-logo {
    height: 100%;
    width: 100%
  }

  .match-top-team {
    display: flex;
    align-items: center
  }

  .flex-right {
    justify-content: flex-end;
  }

  .match-status {
    background-color: white;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    text-align: center;
  }

  .divider {
    display: inline-block;
    min-width: 2rem
  }

  .win {
    color: green;
  }

  .lose {
    color: red;
  }

  @media (max-width: 768px) {
    .match-team-logo-container {
      padding: 0.5rem;
      height: 70px;
      width: 70px;
    }

    .match-team-name {
      display: none;
    }

    .divider {
      min-width: 0.2rem
    }
  }

</style>



