<template>
  <el-collapse class="roster">
    <el-collapse-item>
      <template slot="title">
        <div :style="`border-${borderDirection}: 0.3rem solid ${borderColor};`" class="roster-brief"
             :class="[direction?direction==='left'?'left-roster':'right-roster':'']">
          <player-brief :player="info" :reverse="reverse" :no-border="true" :disabled="true">
            <div
              style="flex-grow: 1;display: flex;justify-content:flex-end;align-items: center; padding: 0.5rem 0 1rem 0.5rem"
              slot="suffix"
              v-if="teamLogo">
              <img :src="teamLogo" style="width: 30px">
            </div>
          </player-brief>
        </div>
      </template>
      <div class="stats">
        <el-table :data="heroes" ref="myTable" :height="tableHeight">
          <el-table-column
            prop="name"
            label="英雄"
            min-width="3"
          >
            <template slot-scope="scope">
              <div v-if="scope.row.name==='ALL'">ALL</div>
              <div class="hero-portrait-image-mask" v-else>
                <div :style="`background-image: url(${getHeroIcon(scope.row.name)})`"
                     class="hero-portrait-image"></div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="eliminations"
            label="消灭"
            :formatter="toFixed2"
            min-width="2"
          >
          </el-table-column>
          <el-table-column
            prop="deaths"
            label="阵亡"
            :formatter="toFixed2"
            min-width="2"
          >
          </el-table-column>
          <el-table-column
            prop="damage"
            label="伤害"
            :formatter="toFixed0"
            min-width="3"
          >
          </el-table-column>
          <el-table-column
            prop="healing"
            label="治疗"
            :formatter="toFixed0"
            min-width="3"
          >
          </el-table-column>
        </el-table>

      </div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
  import PlayerBrief from "@/components/Team/PlayerBrief";
  import {heroIcon} from "@/common/util";

  export default {
    name: "Roster",
    components: {PlayerBrief},
    props: ['stats', 'info', 'reverse', 'teamLogo', 'borderColor', 'borderDirection', 'direction', 'fold'],
    computed: {
      isMobile() {
        return this.$store.getters.getWindowParam.width < 768;
      },
      tableHeight() {
        return this.isMobile ? 60 * this.heroes.length + 20 : 100 * (this.heroes.length) + 20
      },
      heroes() {
        const heroes = [];
        this.stats.heroes.forEach(h => {
          const hero = {};
          hero.name = h.name;
          h.stats.forEach(stat => {
            hero[stat.name] = stat.value
          });
          heroes.push(hero)
        });
        const all = {name: 'ALL'};
        this.stats.stats.forEach(stat => {
          all[stat.name] = stat.value
        });
        heroes.push(all);
        return heroes;
      }
    },
    methods: {
      toFixed0(row, column, cellValue) {
        return cellValue === undefined ? 0 : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(0)
      },
      toFixed2(row, column, cellValue) {
        return cellValue === undefined ? 0 : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(2)
      },

      getHeroIcon(heroName) {
        return heroIcon(heroName)
      }
    }

  }
</script>

<style scoped>

</style>
<style>
  .roster {
    margin: .5rem 0;
    border: none;
  }

  .roster-brief {
    flex-grow: 1;
    background-color: #f1f1f1;

  }

  .left-roster {
    padding-right: 1rem;
  }

  .right-roster {
    padding-left: 1rem;

  }

  .roster .el-collapse-item__header {
    height: auto;
    line-height: inherit;
    border: none;
  }

  .roster .el-collapse-item__arrow {
    display: none;
  }

  .roster .el-collapse-item__wrap {
    border: none;
  }

  .stats {
    padding: 0 0.5rem;
  }
</style>



