<template>
  <div v-if="Object.keys(teams).length>0">
    <el-row>
      <el-col :span="4">
        main
      </el-col>
      <el-col :span="4">
        alt
      </el-col>
      <el-col :span="4">
        dark
      </el-col>


    </el-row>
    <div v-for="team in teams" :style="{backgroundColor:team.primaryColor,color:team.fontColor}">
      <el-row>
        <el-col :span="4">
          <img v-if="team.logo.main" :src="team.logo.main.svg">
          <span v-else>not exist</span>
        </el-col>
        <el-col :span="4">
          <img v-if="team.logo.alt" :src="team.logo.alt.svg">
          <span v-else>not exist</span>
        </el-col>
        <el-col :span="4">
          <img v-if="team.logo.altDark" :src="team.logo.altDark.svg">
          <span v-else>not exist</span>
        </el-col>


      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Logo",
    computed: {
      teams() {
        return this.$store.getters.getAllTeams
      },
    }
  }
</script>

<style scoped>
  img {
    width: 100%
  }
</style>



